import React, { useState } from 'react'

const AddNew = (props) => {
    const [nameInput, setNameInput] = useState("")
    const [descriptionInput, setDescriptionInput] = useState("")
    const [noInputError, setNoInputError] = useState(false)

    return (
        <div className="add-new-form">
            <p  className="error-msg"
                style={{visibility: noInputError ? "visible" : "hidden"}}>name mustn't be empty</p>

            <label className="form-label-text" htmlFor="add-task-title">Title: </label>
            <input className="form-input" 
                    id="add-task-title"
                    type="text" value={nameInput} 
                    onChange={(e) => setNameInput(e.target.value)} 
                    placeholder="Enter task name.."
                    autoComplete="off" /> 

            <label className="form-label-text" htmlFor="add-task-description">Description: </label>
            <input className="form-input" 
                    id="add-task-description"
                    type="text" value={descriptionInput} 
                    onChange={(e) => setDescriptionInput(e.target.value)} 
                    placeholder="Optional"
                    autoComplete="off" />
                       
            <div>
                <button className="btn-gn" style={{fontSize: "1.3em"}} onClick={props.closeDialog}><i className="fas fa-times"></i></button>
                <button className="btn-gn" style={{fontSize: "1.3em"}} onClick={addTask}><i className="fas fa-check"></i></button>
            </div>
        </div>
    )

    function addTask() {
        if(nameInput) {
            props.addTask(nameInput, descriptionInput)
            setNameInput("")
            props.closeDialog()
            props.setDashboardVisibility(false);
        }
        else {
            setNoInputError(true)

            setTimeout(() => {
                setNoInputError(false)
            }, 1400);
        }
    }
}

export default AddNew
