import React from 'react'
import AddNew from './AddNew'

class Dashboard extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            showAddNewBox: false,
            current_task: null
        }

        this.showAddNewDialogBox = this.showAddNewDialogBox.bind(this)
        this.selectTask = this.selectTask.bind(this)
        this.taskMarked = this.taskMarked.bind(this)
    }

    componentDidUpdate() {
        if(this.state.current_task !== this.props.current_task)
            this.setState({
                current_task: this.props.current_task
            })
    }

    selectTask(e) {        
        this.props.setCurrentTask(e.target.getAttribute("data-key"));
        this.props.setDashboardVisibility(false);
    }

    render() {
        return (
            <div className="dashboard" style={{transform: this.props.dashboardVisible ? "translateX(0)" : "translateX(-105%)"}}>
                <button className="btn-gn dashboard-close-btn"
                        onClick={() => {
                            this.props.setDashboardVisibility(false);
                            this.showAddNewDialogBox(true);
                            }} 
                        ><i className="fas fa-times"></i></button>

                <p className="dashboard-title">Tasks</p>

                <button className="btn-dashboard create-new-btn"
                        onClick={() => {this.showAddNewDialogBox(false)}}>Create new</button>


                {this.state.showAddNewBox && <AddNew closeDialog={this.showAddNewDialogBox}
                                                     addTask={this.props.addTask}
                                                     setDashboardVisibility={this.props.setDashboardVisibility}/>}

                <span className="horizontal-break"></span>

                <ul>
                    {
                        this.props.tasklist.map(task => {
                            return <li key={task.id}
                                        className={"task-list-item" 
                                                    + (this.state.current_task === task.id ? " task-selected" : "")
                                                    + (this.taskMarked(task))
                                                }
                                        data-key={task.id}
                                        onClick={this.selectTask} >{task.name}</li>
                        })
                    }
                </ul>
            </div>
        )
    }

    showAddNewDialogBox(close) {
        this.setState((state) => ({
            showAddNewBox: close ? false : !state.showAddNewBox
        }))
    }

    taskMarked(task) {
        // (task.dates['2021'] && task.dates['2021']['nov'] && task.dates['2021']['nov'].includes('14') ? " task-marked" : "")
        let date = new Date();

        let [year, month, dayOfMonth] = [date.getFullYear(), date.getMonth(), date.getDate()];
        [year, month, dayOfMonth] = [year.toString(), month.toString(), dayOfMonth.toString()]
        let allMonths = [...this.props.allMonths];
        allMonths.forEach((month, i) => {
            allMonths[i] = month.toLowerCase();
        });

        if(task.dates[year] 
            && task.dates[year][allMonths[month]] 
            && task.dates[year][allMonths[month]].includes(dayOfMonth)) {
                return " task-marked";
            }
        return "";
    }
}

export default Dashboard
