import React, { useState, useEffect } from 'react'

const Rename = (props) => {
    const [nameInput, setNameInput] = useState("")
    const [descriptionInput, setDescriptionInput] = useState("")
    const [showError, setShowError] = useState(false);

    useEffect(() => {
        setNameInput(props.current_task.name)
        setDescriptionInput(props.current_task.description)

    }, [props])

    return (
        <div className="translucent-background">
            <div className="rename-task-box">
                <div className="form">
                    <p className="form-title">Rename</p>
                    <p className="error-msg"
                        style={{visibility: showError ? "visible" : "hidden"}}>Title can't be empty</p>
                    <label className="form-label-text" htmlFor="rename-task-title">Title: </label>
                    <input className="form-input" 
                            id="rename-task-title"
                            type="text" value={nameInput} 
                            onChange={(e) => setNameInput(e.target.value)} 
                            placeholder="Enter task name.." 
                            autoComplete="off"
                            required /> 

                    <label className="form-label-text" htmlFor="add-task-description">Description: </label>
                    <input className="form-input" 
                            id="add-task-description"
                            type="text" value={descriptionInput} 
                            onChange={(e) => setDescriptionInput(e.target.value)} 
                            placeholder="Optional"
                            autoComplete="off" />
                            
                    <div className="buttons">
                        <button className="btn-gn" onClick={closeWindow}><i className="fas fa-times"></i></button>
                        <button className="btn-gn" onClick={updateTask} ><i className="fas fa-check"></i></button>
                    </div>
                </div>
            </div>
        </div>
    )

    function updateTask() {
        if(nameInput.length > 0) {
            props.updateTask(props.current_task.id, nameInput, descriptionInput);
            closeWindow();
        }
        else {
            setShowError(true);
            setTimeout(() => {
                setShowError(false);
            }, 1500);
        }
    }

    function closeWindow() {
        props.setShowSettings(false);
    }
}

export default Rename
