import React from 'react'

class Calendar extends React.Component {
    constructor(props) {
        super(props);

        this.months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        this.longDays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        this.shorDays = ['Sun', 'Mon', 'Tue', 'Wed',' Thu', 'Fri', 'Sat'];
        this.areDaysLong = true;

        this.state = {
            daysOfWeek: window.innerWidth > 600 ? this.longDays : this.shorDays,
            blankDays: 0,
            totalDaysInMonth: 0,
            num_days: []
        }

        this.currdate = null
        this.prevdate = null;

        this.markDay = this.markDay.bind(this)
        this.getCurrentMonthAndYear = this.getCurrentMonthAndYear.bind(this)
        this.updateMarkedDays = this.updateMarkedDays.bind(this)
    }

    componentDidMount() {
        const date = this.props.date;

        this.setState({
            blankDays: this.getBlankDays(date),
            totalDaysInMonth: this.getTotalDaysInMonth(date)
        })

        this.currdate = date;

        this.updateMarkedDays()

        window.addEventListener("resize", () => {
            if(window.innerWidth < 600 && this.areDaysLong) {
                this.setState({daysOfWeek: this.shorDays});
                this.areDaysLong = false;
            }
            else if(window.innerWidth > 600 && !this.areDaysLong) {
                this.setState({daysOfWeek: this.longDays});
                this.areDaysLong = true;
            }
        })
    }

    componentDidUpdate(prevProps) {
        this.prevdate = this.currdate;
        this.currdate = this.props.date;

        if(this.prevdate && (this.prevdate.getTime() !== this.currdate.getTime())) {
            const date = this.props.date;
    
            this.setState({
                blankDays: this.getBlankDays(date),
                totalDaysInMonth: this.getTotalDaysInMonth(date)
            })
        }

        if((prevProps.current_task.id !== this.props.current_task.id) ||
                    (prevProps.current_task.uat !== this.props.current_task.uat) ||
                    (prevProps.date !== this.props.date))
            this.updateMarkedDays()
    }

    render() {
        const today = new Date().getDate();

        return (
            <div className="cal-grid-container">
                {this.state.daysOfWeek.map((day, index) => {
                    return <div key={index}>{day}</div>
                })}
    
                {
                    this.getDays(this.state.totalDaysInMonth).map((day) => {
                        const style = day === 1 ? {gridColumn: `${this.state.blankDays} / ${this.state.blankDays+1}`} : {};
                        const isMarked = this.state.num_days.includes(day.toString()) ? "calendar-date-marked" : "";
                        const isToday = day === today ? "calendar-today" : "";
                        
                        return <div style={style}
                                     key={day} 
                                     data-date={day}
                                     className={`calendar-tile ${isMarked} ${isToday}`}
                                     onContextMenu={(e) => e.preventDefault()} >{day}</div>;
                    })
                }
            </div>
        )
    }

    updateMarkedDays() {
        const [month, year] = this.getCurrentMonthAndYear();
        const monthAlpha = this.months[month].toLowerCase();
        const currTask = this.props.current_task.dates;

        let num_days = [];
        if(currTask[year] && currTask[year][monthAlpha]) {
            num_days = currTask[year][monthAlpha];
        }

        this.setState({
            num_days
        });
    }

    markDay(e) {
        e.preventDefault()
        
        let num_day = e.target.getAttribute('data-date');
        let [month, year] = this.getCurrentMonthAndYear();
        
        this.props.markDay(year, month, num_day)
    }
    
    getCurrentMonthAndYear() {
        let date = new Date(this.props.date);
        let month = date.getMonth();
        let year = date.getFullYear();
        return [month, year];
    }

    getBlankDays(date) {
        date.setDate(1);
        return date.getDay() + 1;
    }
    
    getTotalDaysInMonth(date) {
        const year = date.getFullYear();
        const month = date.getMonth() + 1;
    
        const newDate = new Date(year, month, 0);
        return newDate.getDate();
    }

    getDays(totalDaysInMonth) {
        let days = [];
        
        for(let i=1; i<=totalDaysInMonth; i++) {
            days.push(i);
        }
    
        return days;
    }
}

export default Calendar
