import React from 'react'
import { useState } from 'react'
import DropDown from './dashboard/DropDown'

const Navbar = (props) => {
    const [showSettings, setShowSettings] = useState(false)

    return (
        <React.Fragment>
            <nav className="main-navbar" style={{position: "relative"}}>
                <button className="button-md rotate-btn"
                        style={{visibility: props.dashboardVisible ? "hidden" : "visible"}}
                        onClick={() => props.setDashboardVisibility(true)}><i className="fas fa-bars"></i></button>
                <button className="button-md rotate-btn" onClick={() => {setShowSettings(!showSettings)}}>
                    <i className="fas fa-cog"></i>
                </button>
            </nav>
            {showSettings && <DropDown  setShowSettings={setShowSettings}
                                        current_task={props.current_task}
                                        updateTask={props.updateTask}
                                        deleteTask={props.deleteTask}
                                        markDay={props.markDay}/>}
        </React.Fragment>
        
    )
}

export default Navbar
