import React, { useState, useEffect } from 'react'

const CalendarHeader = (props) => {
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    const monthAndYear = props.date && months[props.date.getMonth()] + " " + props.date.getFullYear();
    
    const [currentTask, setCurrentTask] = useState(props.current_task)
    const [showDescription, setShowDescription] = useState(false)

    useEffect(() => {
        setCurrentTask(props.current_task)

    }, [props.current_task])

    return (
        <div>
            <div className="calender-title">{
                currentTask.name    
            }
                <i className="fas fa-question-circle" style={{marginLeft: "0.8em", fontSize: "0.8em"}} 
                    onMouseEnter={() => {setShowDescription(true)}}
                    onMouseLeave={() => {setShowDescription(false)}}></i>

                {showDescription && <p className="sm-tooltip">{currentTask.description}</p>}
            </div>
            <div className="secondary-navbar">
                <button className="button-md" onClick={() => prevMonth(props.date, props.changeDate)}>
                <i className="fas fa-chevron-left"></i>
                </button>

                <span>{monthAndYear}</span>

                <button className="button-md" onClick={() => nextMonth(props.date, props.changeDate)}>
                <i className="fas fa-chevron-right"></i>
                </button>
            </div>
        </div>
    )
}

function prevMonth(currDate, changeDate) {
    let year = currDate.getFullYear(),
        month = currDate.getMonth(),
        day = currDate.getDate();

    if(month <= 0) {
        year--;
        month = 11;
    }
    else {
        month--;
    }

    changeDate(new Date(year, month, day));
}

function nextMonth(currDate, changeDate) {
    let year = currDate.getFullYear(),
        month = currDate.getMonth(),
        day = currDate.getDate();

    if(month >= 11) {
        year++;
        month = 0;
    }
    else {
        month++;
    }

    changeDate(new Date(year, month, day));
}

export default CalendarHeader
