import React from 'react'

const Delete = (props) => {
    return (
        <div className="translucent-background">
            <div className="form form-sm">
                <p className="form-title m-b">Are you sure to delete ?</p>
                <div className="buttons">
                    <button className="btn-gn" onClick={closeWindow}><i className="fas fa-times"></i></button>
                    <button className="btn-gn" onClick={deleteTask}><i className="fas fa-check"></i></button>
                </div>
            </div>
        </div>
    )

    function deleteTask() {
        props.deleteTask(props.current_task.id);
        closeWindow();
    }

    function closeWindow() {
        props.setShowSettings(false);
    }
}

export default Delete
