import React, {useState} from 'react'
import Rename from './Rename'
import Delete from './Delete';

const DropDown = (props) => {
    const [rename, setRename] = useState(false);
    const [deleteTask, setDeleteTask] = useState(false);

    return (
        <React.Fragment>
            <div className="drop-down-menu">
                <button onClick={() => {setRename(!rename)}}>Rename</button>
                <button onClick={() => {setDeleteTask(!deleteTask)}}>Delete</button>
                <button onClick={markToday}>Mark Today</button>
            </div>

            {rename && <Rename current_task={props.current_task}
                                updateTask={props.updateTask}
                                setShowSettings={props.setShowSettings}/>}
                            
            {deleteTask && <Delete current_task={props.current_task}
                                    deleteTask={props.deleteTask}
                                    setShowSettings={props.setShowSettings}/>}

        </React.Fragment>
    )

    function markToday() {
        let today = new Date();
        let year = today.getFullYear();
        let month = today.getMonth();
        let dayOfMonth = today.getDate();

        props.markDay(year, month, dayOfMonth);
        props.setShowSettings(false)
    }
}

export default DropDown