import React from 'react';
import {v4 as uuidv4} from "uuid"
import './App.css';
import Navbar from './components/Navbar';
import Calendar from './components/Calendar';
import CalendarHeader from './components/CalendarHeader';
import Dashboard from './components/dashboard/Dashboard';


/*
=============================BUGS=====================================================
changing task doesn't update calendar component
======================================================================================
*/

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      displayDate: new Date(),
      calendar_data: {
        tasks: []
      },
      current_task: null,
      dashboardVisible: false,
    }

    this.months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    this.changeDate = this.changeDate.bind(this)
    this.addNewTaskToState = this.addNewTaskToState.bind(this)
    this.setDashboardVisibility = this.setDashboardVisibility.bind(this)
    this.setCurrentTask = this.setCurrentTask.bind(this)
    this.updateLocalStorage = this.updateLocalStorage.bind(this)
    this.markDay = this.markDay.bind(this)
    this.getCurrentTask = this.getCurrentTask.bind(this)
    this.updateTask = this.updateTask.bind(this)
    this.deleteTask = this.deleteTask.bind(this)
    this.alphasort = this.alphasort.bind(this)
  }

  componentDidMount() {
    let localData = JSON.parse(localStorage.getItem("calendar-data"));

    if(localData) {
      this.setState({
        calendar_data: {
          tasks: localData.tasks
        },
        current_task: localData.currTask || ( localData.tasks.length && localData.tasks[0].id )
      })
    }
    else {
      let dataStorage = {
        tasks: [],
        currTask: null
      }

      this.updateLocalStorage(dataStorage);
    }
  }

  render() {
    return (
      <React.Fragment>
        <Navbar dashboardVisible={this.state.dashboardVisible}
                setDashboardVisibility={this.setDashboardVisibility}
                current_task={this.getCurrentTask()}
                updateTask={this.updateTask} 
                deleteTask={this.deleteTask}
                markDay={this.markDay} />

        <Dashboard addTask={this.addNewTaskToState}
                   tasklist={this.alphasort(this.state.calendar_data.tasks)} 
                   current_task={this.state.current_task}
                   dashboardVisible={this.state.dashboardVisible}
                   setDashboardVisibility={this.setDashboardVisibility}
                   setCurrentTask={this.setCurrentTask}
                   allMonths={this.months} />

        {this.state.current_task ? <CalendarHeader date={this.state.displayDate} 
                                                    changeDate={this.changeDate} 
                                                    current_task={this.getCurrentTask()}/> : null}
        
        {this.state.current_task ? <Calendar date={this.state.displayDate}
                                              current_task={this.getCurrentTask()}
                                              markDay={this.markDay}/> : null}
        
      </React.Fragment>  
    )
  }

  alphasort(arr) {
    let newArr = JSON.parse(JSON.stringify(arr));
    newArr.sort((a, b) => {
      a = a.name.toLowerCase();
      b = b.name.toLowerCase();

      if(a < b) return -1;
      if(a > b) return 1;
      return 0;
    })
    return newArr;
  }

  getCurrentTask() {
    return this.state.calendar_data.tasks.filter(task => {
      return task.id === this.state.current_task
    })[0]
  }

  updateLocalStorage(data) {
    localStorage.setItem("calendar-data", JSON.stringify(data));
  }

  changeDate(date) {
    this.setState({
      displayDate: date
    })
  }

  addNewTaskToState(title, description) {
    let toAdd = {
      id: uuidv4(),
      name: title,
      description: description,
      uat: new Date(),
      dates: {}
    }

    this.setState(state => ({
      calendar_data: {
        tasks: [...state.calendar_data.tasks, toAdd], 
      },
      current_task: toAdd.id,
      //dashboardVisible: false
    }), () => {

      let dataStorage = {
        tasks: this.state.calendar_data.tasks,
        currTask: this.state.current_task
      }

      this.updateLocalStorage(dataStorage);
    })
  }

  updateTask(id, name, description) {
    const toUpdate = this.state.calendar_data.tasks.filter(task => {
      return task.id === id
    })[0];

    const newTask = JSON.parse(JSON.stringify(toUpdate));
    newTask.name = name;
    newTask.description = description;

    const allTasks = this.state.calendar_data.tasks.filter(task => {
      return task.id !== id
    });

    allTasks.push(newTask);

    this.setState({
      calendar_data: {
        tasks: allTasks
      }
    }, () => {
      this.updateLocalStorage({
        tasks: this.state.calendar_data.tasks,
        currTask: this.state.current_task
      })
    })
  }

  deleteTask(id) {
    const allTasks = this.state.calendar_data.tasks.filter(task => {
      return task.id !== id
    });

    this.setState({
      calendar_data: {
        tasks: allTasks
      },
      current_task: allTasks.length > 0 ? allTasks[0].id : null 
    }, () => {
      this.updateLocalStorage({
        tasks: this.state.calendar_data.tasks,
        currTask: this.state.current_task
      })
    })
  }

  setDashboardVisibility(value) {
    this.setState({
      dashboardVisible: value
    })
  }

  setCurrentTask(task) {
    this.setState({
      current_task: task
    }, () => {
      
      let dataStorage = {
        tasks: this.state.calendar_data.tasks,
        currTask: this.state.current_task
      }

      this.updateLocalStorage(dataStorage);

    })
  }

  markDay(year, month, num_day) {
    year = year.toString();
    month = month.toString();
    num_day = num_day.toString();

    let monthAlpha = this.months[month].toLowerCase();
    let taskToMarkOrig = this.state.calendar_data.tasks.filter(task => {
      return task.id === this.state.current_task
    })[0]

    let taskToMark = JSON.parse(JSON.stringify(taskToMarkOrig));

    if(!taskToMark.dates[year]) 
      taskToMark.dates[year] = {};
    if(!taskToMark.dates[year][monthAlpha]) 
      taskToMark.dates[year][monthAlpha] = []
    if(!taskToMark.dates[year][monthAlpha].includes(num_day))
      taskToMark.dates[year][monthAlpha].push(num_day);
    else {
      let months = [];
      taskToMark.dates[year][monthAlpha].forEach(day => {
        if(day !== num_day)
          months.push(day);
      }) 

      taskToMark.dates[year][monthAlpha] = months;
    }

    let allTasks = this.state.calendar_data.tasks.filter(task => {
      return task.id !== this.state.current_task
    });

    taskToMark.uat = new Date();

    allTasks.push(taskToMark);

    this.setState({
      calendar_data: {
        tasks: allTasks
      }
    }, () => {

      this.updateLocalStorage({
        tasks: this.state.calendar_data.tasks,
        currTask: this.state.current_task
      })

    })
  }
}

export default App;